@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Schedule{display:flex;flex-direction:row;font-size:20px;width:100%;font-family:Inter, sans-serif}.Schedule .MainBar{margin:0.75em}.Schedule .MainBar .Title{padding-right:0.3em;font-weight:700;display:inline}.Schedule .MainBar .Countdown{font-weight:300;display:inline}

